function mostrarAlerta(mensaje, tipo){

    //si hay una alerta previa, no mostrar otra
    const alertaPrevia = document.querySelector('.alerta');
    if(alertaPrevia){
        return;
    }

    const alertaMensaje = document.createElement('P');
    alertaMensaje.textContent = mensaje;

    const alerta = document.createElement('DIV');
    alerta.appendChild(alertaMensaje);
    alerta.classList.add('alerta');

    if (tipo = 'error'){
        alerta.classList.add('error')
    }

    //insertar en formulario
    const formulario = document.querySelector('.formulario');
    formulario.appendChild(alerta);

    //eliminar la alerta despues de 3 segundos
    setTimeout(()=>{
        alerta.remove();
    }, 3000)
}

function fechaCita(){
    const fechaInput = document.querySelector('#fecha');
    fechaInput.addEventListener('input', e => {

        const dia = new Date(e.target.value.replace(/-/g, '\/').replace(/T.+/, '')).getUTCDay();

        if([0, 6].includes(dia)){
            e.preventDefault();
            fechaInput.value = '';
            mostrarAlerta('Fines de semana no trabajamos', 'error');
        } else {
            cita.fecha = fechaInput.value;
        }
/* 
        const opciones = {
            weekday: 'long',
            year: 'numeric',
            month: 'long'
        }

        console.log(dia.toLocaleDateString('es-ES', opciones)); */
    })
}


function deshabilitarFechaAnterior(){
    const inputFecha = document.querySelector('#fecha');

    const fechaActual = new Date();
    const year = fechaActual.getFullYear();
    const mes = ("0" + (fechaActual.getMonth() +1)).slice(-2) ;
    const dia = ("0" + (fechaActual.getDate() +1)).slice(-2) ;

    //Formato desaro AAAA-MM-DD
    const fechaDeshabilitar = `${year}-${mes}-${dia}`;

    inputFecha.min = fechaDeshabilitar;
}

function horaCita(){
    const inputHora =document.querySelector('#hora');
    inputHora.addEventListener('input', e=>{

        const horaCita = e.target.value;
        const hora = horaCita.split(':');

        if( hora[0] < 10 || hora[0] > 18){
            mostrarAlerta('Hora no valida', 'error');
            setTimeout(()=>{
                inputHora.value = '';
            }, 3000);
        } else{
            cita.hora = horaCita;
            console.log(cita);
        }
    })
}